*,
*:after,
*:before {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0;
}

body {
  padding: 0;
  font-family: sans-serif;
}

#root {
  padding: 2rem;
}

#quote-box {
  padding: 1em;
  max-width: 500px;
  min-height: 200px;
  margin: auto;
  border-radius: 10px;
  -webkit-box-shadow: 1px 1px 2px 2px #666;
          box-shadow: 1px 1px 2px 2px #666;
  background: #fff;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1em;
}

#text, #author {
  grid-column: span 2;
}
#text {
  font-size: 1.5em;
}

button {
  width: 100%;
  padding: 1em;
  font-weight: bold;
  border-radius: 10px;
  cursor: pointer;
  height: 70px;
  border: none;
  -webkit-box-shadow: 1px 1px 2px 2px #666;
          box-shadow: 1px 1px 2px 2px #666;
}

footer {
  text-align: center;
}

button, footer, a {
  color: #fff;
}


